/**
*--------------------------------------------------------------------------
* App Scripts
*--------------------------------------------------------------------------
*
* Scripts a compilar por la aplicacin
*/

/** Requerimiento del paquete bootstrap 4 para el diseo de la aplicacin */
require('./bootstrap');
//require('./loading-message');

/** @type {object} Requerimiento del paquete vue para la reactividad de la aplicacin */
window.Vue = require('vue');

/** @type {Date} Ao de ejecucin */
window.execution_year = new Date().getFullYear();

if (window.auth) {
    /** Busca el ao de ejecucin solo cuando el usuario se encuentra autenticado */
    axios.get('/get-execution-year', {}).then(response => {
        if (response.data.result) {
            window.execution_year = response.data.year;
        }
    }).catch(error => {
        console.log(error);
    });
}
Vue.use(window.execution_year);
